  components: {
    slider
  },
  methods: {
    goIndex () {
      this.$router.push({name: 'photos'})
    },
    initData () {
      const article = this.article
      const end = this.article.length
      const id = this.$route.query.id
      for (let i = 0; i < end; i++) {
        if (article[i].id == id) {
          let obj = {
            id: article[i].id,
            title: article[i].title,
            subtitle: article[i].subtitle,
            photolist: []
          }
          this.photolists = article[i].photolist
          for (let j = 0; j < 3; j++) {
            obj.photolist.push(article[i].photolist[j])
          }
          this.activeArticle = obj
          return true
        }
      }
    },
    handleScroll () {
      const offset = 50
      let windowHeight = this.getWindowHeight()
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      if (scrollTop + windowHeight > this.$refs.photoLists.offsetHeight - offset) {
        // 触底加载
        this.addActiveArticle()
      }
    },
    addActiveArticle () {
      console.log('触发加载')
      let step = 2
      const end = this.photolists.length

      if (this.loadCount + 2 > end) {
        step = this.photolists.length
      } else {
        step = this.loadCount + 2
      }

      while (this.loadCount < step) {
        this.activeArticle.photolist.push(this.photolists[this.loadCount++])
      }
    },
    getWindowHeight () {
      var windowHeight = 0
      if (document.compatMode == 'CSS1Compat') {
        windowHeight = document.documentElement.clientHeight
      } else {
        windowHeight = document.body.clientHeight
      }
      return windowHeight
    }
    // preload() {
